<h1 id="vue基本概念" tabindex="-1"><a class="header-anchor" href="#vue基本概念" aria-hidden="true">#</a> vue基本概念</h1>
<h2 id="vue介绍" tabindex="-1"><a class="header-anchor" href="#vue介绍" aria-hidden="true">#</a> vue介绍</h2>
<ul>
<li><a href="https://cn.vuejs.org/" target="_blank" rel="noopener noreferrer">vue 中文网<ExternalLinkIcon/></a>  尤雨溪</li>
<li><a href="https://cn.vuejs.org/v2/guide/index.html#Vue-js-%E6%98%AF%E4%BB%80%E4%B9%88" target="_blank" rel="noopener noreferrer">Vue.js 是什么<ExternalLinkIcon/></a></li>
<li>Vue (读音 /vjuː/，类似于 <strong>view</strong>) 是一套用于构建用户界面的<strong>渐进式javascript框架</strong>。</li>
</ul>
<h3 id="渐进式的概念" tabindex="-1"><a class="header-anchor" href="#渐进式的概念" aria-hidden="true">#</a> 渐进式的概念</h3>
<p>渐进式：逐渐增强，可以在项目中使用vue的一部分功能，也可以使用vue的全家桶来管理整个项目。</p>
<p>angular: 全家桶</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="" originSrc="images/%E6%B8%90%E8%BF%9B%E5%BC%8F.png" data="aurora"></p>
<h3 id="框架的概念" tabindex="-1"><a class="header-anchor" href="#框架的概念" aria-hidden="true">#</a> 框架的概念</h3>
<ul>
<li><a href="https://zhuanlan.zhihu.com/p/26078359?group_id=830801800406917120" target="_blank" rel="noopener noreferrer">我们所说的前端框架与库的区别？<ExternalLinkIcon/></a></li>
</ul>
<p><strong>Library</strong></p>
<ul>
<li>
<p>代表：moment  axios</p>
</li>
<li>
<p>库，本质上是一些函数的集合。每次调用函数，实现一个特定的功能   工具箱</p>
</li>
</ul>
<ul>
<li>使用库的时候，把库当成工具使用，需要自己控制代码的执行逻辑。</li>
</ul>
<p><strong>Framework</strong></p>
<ul>
<li>
<p>代表：vue、angular、react、bootstrap</p>
</li>
<li>
<p>框架，是一套完整的解决方案</p>
</li>
</ul>
<ul>
<li>使用框架的时候，框架实现了大部分的功能，我们只需要按照框架的规则写代码</li>
</ul>
<p><strong>库和框架的区别</strong></p>
<ul>
<li>使用库的时候，很自由，只要调用库提供的各种各样的方法就行，也可以不用其他的一些方法</li>
<li>使用框架的时候，需要按照框架的规则写代码，限制会非常多，但同时框架的功能也很强大，可以极大的提升开发的效率。</li>
</ul>
<h3 id="vue是-mvvm-的框架" tabindex="-1"><a class="header-anchor" href="#vue是-mvvm-的框架" aria-hidden="true">#</a> vue是 MVVM 的框架</h3>
<ul>
<li>MVVM思想：一种软件架构模式，决定了写代码的方式。
<ul>
<li>M：model数据模型(ajax获取到的数据)</li>
<li>V：view视图（页面）</li>
<li>VM：ViewModel 视图模型</li>
</ul>
</li>
</ul>
<ul>
<li>MVVM通过<code>数据双向绑定</code>让数据自动地双向同步  <strong>不在需要操作DOM</strong>
<ul>
<li>V（修改视图） -&gt; M（数据自动同步）</li>
<li>M（修改数据） -&gt; V（视图自动同步）</li>
</ul>
</li>
</ul>
<p><strong>1. 在vue中，不推荐直接手动操作DOM！！！</strong></p>
<p><strong>2. 在vue中，通过数据驱动视图，不要在想着怎么操作DOM，而是想着如何操作数据！！</strong></p>
<h2 id="开发vue的方式" tabindex="-1"><a class="header-anchor" href="#开发vue的方式" aria-hidden="true">#</a> 开发vue的方式</h2>
<p>开发vue有两种方式</p>
<ul>
<li>传统开发模式：基于html/css/js文件开发vue</li>
<li>工程化开发方式：在webpack环境中开发vue，这是最推荐的方式。</li>
<li>现代化的项目也都是在webpack环境下进行开发的。</li>
</ul>
<h1 id="vue-cli的使用" tabindex="-1"><a class="header-anchor" href="#vue-cli的使用" aria-hidden="true">#</a> vue-cli的使用</h1>
<blockquote>
<p><code>vue-cli</code>也叫vue脚手架,<code>vue-cli</code>是vue官方提供的一个全局命令工具，这个命令可以帮助我们快速的创建一个vue项目的基础架子。</p>
</blockquote>
<ul>
<li>开箱即用</li>
<li>零配置</li>
<li>webpack、babel</li>
</ul>
