</ul>
<h2 id="vue单文件组件的说明" tabindex="-1"><a class="header-anchor" href="#vue单文件组件的说明" aria-hidden="true">#</a> vue单文件组件的说明</h2>
<p>一个<code>.vue</code>文件就是一个组件,后续开发vue，所有的功能都是基于组件实现。</p>
<p>安装插件</p>
<p>一个单文件组件由三部分构成</p>
<ul>
<li>template(必须)  影响组件渲染的结构  html
<ul>
<li>只能有一个根元素</li>
</ul>
</li>
<li>script                     逻辑   js</li>
<li>style                       样式   css less scss
<ul>
<li>style用于提供组件的样式，默认只能用css</li>
<li>可以通过<code>lang=&quot;less&quot;</code>开启less的功能，需要安装依赖包</li>
</ul>
</li>
</ul>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>yarn add less-loader@7.2.1 less -D
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h1 id="vue的插值表达式" tabindex="-1"><a class="header-anchor" href="#vue的插值表达式" aria-hidden="true">#</a> vue的插值表达式</h1>
<h2 id="vue通过data提供数据" tabindex="-1"><a class="header-anchor" href="#vue通过data提供数据" aria-hidden="true">#</a> vue通过data提供数据</h2>
<blockquote>
<p>vue中通过template可以提供模板，但是这样的数据是写死的。</p>
</blockquote>
<p>vue可以通过data提供数据，注意：<code>data必须是一个函数，并且返回一个对象</code></p>
<div class="language-jsx ext-jsx line-numbers-mode"><pre v-pre class="language-jsx"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>script</span><span class="token punctuation">></span></span><span class="token plain-text">
export default {
  data () {
    return {
      money: 100,
      msg: 'hello'
    }
  }    
}
</span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>script</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div><h2 id="通过插值表达式显示数据" tabindex="-1"><a class="header-anchor" href="#通过插值表达式显示数据" aria-hidden="true">#</a> 通过插值表达式显示数据</h2>
<p>插值表达式, 小胡子语法  mustach语法<code> {{  }}</code></p>
<ol>
<li>
<p>作用:  使用 data 中的数据渲染视图（模板）</p>
</li>
<li>
<p>基本语法, 支持三元运算符</p>
<div class="language-jsx ext-jsx line-numbers-mode"><pre v-pre class="language-jsx"><code><span class="token punctuation">{</span><span class="token punctuation">{</span> msg <span class="token punctuation">}</span><span class="token punctuation">}</span>
<span class="token punctuation">{</span><span class="token punctuation">{</span> obj<span class="token punctuation">.</span>name <span class="token punctuation">}</span><span class="token punctuation">}</span>
<span class="token punctuation">{</span><span class="token punctuation">{</span> msg<span class="token punctuation">.</span><span class="token function">toUpperCase</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token punctuation">}</span><span class="token punctuation">}</span>
<span class="token punctuation">{</span><span class="token punctuation">{</span> obj<span class="token punctuation">.</span>age <span class="token operator">></span> <span class="token number">18</span> <span class="token operator">?</span> <span class="token string">'成年'</span> <span class="token operator">:</span> <span class="token string">'未成年'</span> <span class="token punctuation">}</span><span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div></li>
<li>
<p>vue中插值表达式的注意点</p>
<p>(1)  使用的数据在 data 中要存在</p>
<div class="language-jsx ext-jsx line-numbers-mode"><pre v-pre class="language-jsx"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>h1</span><span class="token punctuation">></span></span><span class="token punctuation">{</span><span class="token punctuation">{</span> gaga <span class="token punctuation">}</span><span class="token punctuation">}</span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>h1</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>(2)  能使用表达式, 但是不能使用 if  for</p>
<div class="language-jsx ext-jsx line-numbers-mode"><pre v-pre class="language-jsx"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>h1</span><span class="token punctuation">></span></span><span class="token punctuation">{</span><span class="token punctuation">{</span> <span class="token keyword">if</span> <span class="token punctuation">(</span>obj<span class="token punctuation">.</span>age <span class="token operator">></span> <span class="token number">18</span> <span class="token punctuation">)</span> <span class="token punctuation">{</span> <span class="token punctuation">}</span>   <span class="token punctuation">}</span><span class="token punctuation">}</span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>h1</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>(3)  不能在标签属性中使用</p>
<div class="language-jsx ext-jsx line-numbers-mode"><pre v-pre class="language-jsx"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>h1</span> <span class="token attr-name">id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>box<span class="token punctuation">"</span></span> <span class="token attr-name">class</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>box<span class="token punctuation">"</span></span> <span class="token attr-name">title</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>{{ msg }}<span class="token punctuation">"</span></span><span class="token punctuation">></span></span><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>h1</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div></li>
</ol>
<h2 id="安装vue开发者工具" tabindex="-1"><a class="header-anchor" href="#安装vue开发者工具" aria-hidden="true">#</a> 安装vue开发者工具</h2>
<ul>
<li>直接通过谷歌应用商店安装  需要梯子</li>
<li>通过极简插件下载插件，本地安装。 https://chrome.zzzmh.cn/index</li>
